body {
  font-family: "Noto Sans", sans-serif, "Noto Sans JP", sans-serif;
  -webkit-animation: fadeIn 2s;
          animation: fadeIn 2s;
}
body::after {
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: -webkit-gradient(linear, right top, left top, from(#292065), to(#be1fbe));
  background: linear-gradient(-90deg, #292065, #be1fbe);
  z-index: -1;
}
.about {
  background: #ffffff;
  padding: 15px;
}
.about-image {
    margin-bottom: 20px;
}
.about-image img {
      width: 100%;
}
.about-title {
    font-size: 1.4rem;
    margin-bottom: 10px;
    font-weight: bold;
    display: inline-block;
    -webkit-transition: all .3s;
    transition: all .3s;
}
@media screen and (max-width: 400px) {
.about-title {
        font-size: 1.2rem;
}
}
.about-title:hover {
      color: #b851b8;
}
.about-url {
    display: inline-block;
    color: #aaaaaa;
    margin-bottom: 10px;
    font-size: 1.1rem;
    width: 100%;
    overflow-wrap: break-word;
}
@media screen and (max-width: 400px) {
.about-url {
        font-size: .9rem;
        margin-bottom: 7px;
}
}
.about-url:hover {
      color: #b851b8;
}
.about-category {
    margin-bottom: 10px;
}
.about-category-inner {
      font-size: 13px;
      font-weight: bold;
      display: inline-block;
      margin-right: 2.5px;
      background: #D534D5;
      color: #ffffff;
      padding: 5px 7px;
      -webkit-transition: all .3s;
      transition: all .3s;
}
@media screen and (max-width: 400px) {
.about-category-inner {
          padding: 3px 5px;
}
}
.about-category-inner:hover {
        opacity: 0.7;
}
.about-content {
    color: #504e4e;
    font-size: 14px;
    line-height: 1.6;
    margin-bottom: 10px;
}
@media screen and (max-width: 400px) {
.about-content {
        margin-bottom: 7px;
        font-size: 12px;
}
}
.about-date {
    margin-bottom: 10px;
    font-size: 12px;
    color: #666666;
}
@media screen and (max-width: 400px) {
.about-date {
        margin-bottom: 7px;
        font-size: 10px;
}
}
.router-link-active {
  opacity: .5;
}
.load {
  color: #ffffff;
  height: 30vh;
  line-height: 30vh;
  width: 100%;
  text-align: center;
  font-size: 20px;
  -webkit-animation: loading infinite alternate .5s;
          animation: loading infinite alternate .5s;
  margin-top: 30px;
}
@-webkit-keyframes back {
0% {
    -webkit-transform: scaleX(0.9);
            transform: scaleX(0.9);
}
50% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}
100% {
    -webkit-transform: scaleX(0.9);
            transform: scaleX(0.9);
}
}
@keyframes back {
0% {
    -webkit-transform: scaleX(0.9);
            transform: scaleX(0.9);
}
50% {
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
}
100% {
    -webkit-transform: scaleX(0.9);
            transform: scaleX(0.9);
}
}
.back {
  display: inline-block;
  color: #ffffff;
  font-size: 18px;
  margin: 20px;
  -webkit-transform-origin: right;
          transform-origin: right;
  -webkit-animation: back 0.5s infinite cubic-bezier(0.64, -0.05, 0.33, 0.76);
          animation: back 0.5s infinite cubic-bezier(0.64, -0.05, 0.33, 0.76);
  opacity: 1;
}
